.intro {
	min-height: 100vh;
	text-align: left;
	position: relative;
	display: flex;
	width: 100%;
	flex-direction: column;
	justify-content: center;
	background-image: url('../../images/bg-intro.jpg');

	&__title {
		font-size: 4.15em;
		line-height: 1.2em;
		font-family: $f-bold;
		color: $c-accent1;
		margin-bottom: 32px;
		position: relative;
		span {
			display: inline-block;
			transform-origin: 0 0;
		}		
	}

	&__mask {
		overflow: hidden;
		position: relative;
		&:nth-child(1n) span{			
			animation-delay: 3.5s;
		}
		&:nth-child(2n) span{
			animation-delay: 4s;
		}
		&:nth-child(3n) span{
			animation-delay: 4.5s;
		}
	}

	&__text {
		position: relative;
		animation: maskMove 1s forwards;
		animation-duration: 1s;
		transform: translateY(100%) rotate(5deg);
	}

	@keyframes maskMove {
		0% {
			transform: translateY(100%) rotate(5deg);
		}
		100% {
			transform: translateY(0) rotate(0);
		}
	}

	h2 {
		font-size: 2em;
		line-height: 1.2em;
		font-family: $f-default;
		color: $c-accent1;
		opacity: 0;
		max-width: 75%;
		position: relative;
		transform: translateY(50px);
		animation: introFadeUp 750ms 5.5s forwards;
	}

	@keyframes introFadeUp {
		from {
			opacity: 0;
			transform: translateY(50px);
		}
		to {
			transform: translateY(0px);
			opacity: 1;
		}
	}

	.arrowDown {
		position: absolute;
		bottom: 0;
		left: 50%;
		opacity: 0;
		transform: translateX(-50%);
		animation: arrowDown 750ms 6s forwards, upDown 1s 6.5s ease-in-out alternate infinite;
	}
	

	@keyframes arrowDown {
		from {
			opacity: 0;
		}
		to {
			opacity: 1;
		}
	}

	@keyframes upDown {
		from {
			bottom: 0;
		}
		to {
			bottom: 15px;
		}
	}

	.circleDown {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		svg path {
			stroke-dasharray: 1000;
			stroke-dashoffset: -1000;
			animation: circleDown 1.5s  5s ease-in-out forwards;
		}
	} 

	@keyframes circleDown {
		from {
			stroke-dashoffset: -1000;
		}
		to {
			stroke-dashoffset: 0;
		}
	}
}

.half {
	padding: 200px 0;
	.content-wrapper {
		display: flex;
		width: 100%;
		justify-content: space-between;
	}

	&__title {
		width: 50%;
	}

	&__content {
		width: 50%;
		p {
			font-size: 2em;
			line-height:  1.2em;
			font-family: $f-default;
			color: $c-accent2;
			margin-bottom: 40px;
		}		
	}

	h3 {
		font-size: 4.15em;
		line-height: 1.2em;
		font-family: $f-bold;
		color: $c-accent2;
	}
}

.scroll-separator h2 {
	text-transform:uppercase;
	display: inline-block;
	white-space: nowrap;
	animation: floatText 20s infinite linear;
	padding-left: 100%; /*Initial offset, which places the text off-screen*/
	font-size: 10.15em;
	line-height: 1.2em;
	font-family: $f-bold;
	color: $c-accent1;
	opacity: 0.5;

	@keyframes floatText {
		to {
			transform: translateX(-100%);
		}
	}
}

.projects {
	margin-top: -100px;
	&__block {
		text-decoration: none;
		position: relative;
		margin-bottom: 280px;
		&:hover {
			cursor: url('../../images/cursor.svg'), auto;
			img {
				transform: scale(1.05);
			}
		}
		img {
			transition: transform 500ms ease-in-out;
		}

		h3 {
			font-size: 4.15em;
			line-height: 1.2em;
			font-family: $f-bold;
			color: $c-accent2;
		}

		h4 {
			font-size: 2.5em;
			line-height: 1.2em;
			font-family: $f-default;
			color: $c-accent2;
		}

		&--title {
			position: absolute;
			right: 0;
			bottom: 50px;
			// transform: translateX(-50%);
			text-align: right;
		}
	}

	.externalLinks {
		display: block;
		margin: 400px auto;
		text-align: center;
		font-size: 2em;
		line-height: 1.2em;
	}
}


.sideHustle {

	text-align: center;

	h2 {
		font-size: 3.5em;
		line-height: 1.2em;
		font-family: $f-bold;
		color: $c-accent2;
		margin-bottom: 21px;
	}

	p {
		max-width: 750px;
		margin: 0 auto 57px;
		font-size: 2em;
		line-height: 1.2em;
		font-family: $f-bold;
		color: $c-accent2;
	}
}

.art-grid { 
	.masonry-grid {
		display: -webkit-box; /* Not needed if autoprefixing */
		display: -ms-flexbox; /* Not needed if autoprefixing */
		display: flex;
		margin-left: -30px; /* gutter size offset */
		width: auto;
	  }
	  .masonry-grid_column {
		padding-left: 30px; /* gutter size */
		background-clip: padding-box;
	  }
	  
	  /* Style your items */
	  .masonry-grid_column > img { /* change div to reference your elements you put in <Masonry> */
		margin-bottom: 75px;
	  }
}

.contact__block {
	padding: 200px 0px;
	position: relative;
	background-color: $c-backround-intro;
	z-index: -1;

	&:hover {
		cursor: url('../../images/pencil.svg') 0 50, auto !important;
	}

	h2 {
		color: $c-accent3;
		font-size:  3.5em;
		line-height: 1.2em;
		font-family: $f-bold;
		max-width: 640px;
		text-align: center;
		margin: 0 auto 40px;
	}

	#sheetWrapper {
		position: relative;
	}
	.canvas-container {
		position: absolute !important;
		top:0;
		left: 0;
		&:hover {
			cursor: url('../../images/pencil.svg') 0 50, auto;
		}
	}
}

.formBlock {
	display: flex;
	justify-content: center;

	
	.block {
		border: 2px solid $c-accent2;
		width: 72px;
		height: 72px;
	}

	div {
		&:first-child {
			padding-right: 67px;
		}
		span {
			display: block;
			text-align: center;
			font-size: 2em;
			line-height: 1.2em;
			font-family: $f-default;
			color: $c-accent2;
		}
	}
}

.socials-wrapper {
	padding: 0px 0px 200px;
	position: relative;
	background-color: $c-backround-intro;
	.socials {
		text-align: center;
		p {
			padding: 80px 0 32px;
			font-size: 1.5em;
			line-height: 1.2em;
			font-family: $f-default;
			color: $c-accent3;
			a {
				color: $c-accent3;
			}
		}
	
		h3 {
			font-size: 1.5em;
			line-height: 1.2em;
			font-family: $f-default;
			color: $c-accent3;
			margin-bottom: 16px;
		}
	
		&__buttons {
			display: flex;
			justify-content: center;
			align-items: center;
			a {
				margin-left: 16px;
				max-width: 24px;
				border-radius: 50%;
				border: 1px solid transparent;
				transform: opacity 750ms ease-in-out;
	
				&:hover {
					opacity: 0.8;
				}
	
				svg { 
					width: 100%;
				}
			}
		}
	}
}


